"use client";

import { useSpeechRecognition } from "@/hooks/useSpeechRecognition";
import { useSpeechSynthesis } from "@/hooks/useSpeechSynthesis";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Mic, MicOff, Volume2 } from "lucide-react";
import type { LucideProps } from "lucide-react";
import { useState } from "react";
import { cn } from "@/lib/utils";

// Cast for React 19 + lucide-react type compatibility
const iconProps: LucideProps = { className: "size-4", "aria-hidden": true };
const MicIcon = () => <Mic {...iconProps} />;
const MicOffIcon = () => <MicOff {...iconProps} />;
const Volume2Icon = () => <Volume2 {...iconProps} />;

type VoiceSectionProps = {
  /** When user finishes speaking (final transcript), call with the text (e.g. to submit as chat message). */
  onTranscript?: (text: string) => void;
  /** Optional text to speak (e.g. assistant reply). When set, a "Speak" control is shown. */
  textToSpeak?: string;
  className?: string;
};

export function VoiceSection({ onTranscript, textToSpeak, className }: VoiceSectionProps) {
  const [speakInput, setSpeakInput] = useState("");

  const { speak: speakText } = useSpeechSynthesis();

  const {
    isSupported: sttSupported,
    isListening,
    transcript,
    interimTranscript,
    error: sttError,
    start,
    stop,
    reset,
  } = useSpeechRecognition({ continuous: true });

  const handleToggleMic = () => {
    if (isListening) {
      stop();
      const full = (transcript + " " + interimTranscript).trim();
      if (full && onTranscript) onTranscript(full);
    } else {
      reset();
      start();
    }
  };

  const handleSpeakInput = () => {
    const t = speakInput.trim() || textToSpeak;
    if (t) speakText(t);
  };

  const displayTranscript = (transcript + " " + interimTranscript).trim() || "—";

  // Unknown support (server + first client render): render same structure as supported so hydration matches
  if (sttSupported === null) {
    return (
      <Card className={cn("rounded-xl border-border/80 shadow-sm", className)} role="region" aria-labelledby="voice-section-title">
        <CardHeader className="pb-2">
          <CardTitle id="voice-section-title" className="text-base font-medium text-foreground">Voice input</CardTitle>
          <CardDescription className="text-sm">
            Speak your query or hear findings read aloud.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <div className="flex flex-wrap items-center gap-2 sm:flex-nowrap">
              <Button type="button" variant="default" size="icon" className="min-h-[44px] min-w-[44px] touch-manipulation sm:min-h-0 sm:min-w-0" disabled aria-label="Start listening">
                <MicIcon />
              </Button>
              <span className="text-sm text-muted-foreground">Loading…</span>
            </div>
            <div className="min-h-10 rounded-lg border border-input bg-background/50 px-3 py-2 text-sm" role="status" aria-live="polite" aria-label="Live transcript">
              —
            </div>
          </div>
          <div className="space-y-2">
            <label htmlFor="voice-speak-input" className="text-sm font-medium">Text to speak</label>
            <div className="flex flex-wrap gap-2 sm:flex-nowrap">
              <Input id="voice-speak-input" type="text" placeholder="Enter text to hear it read aloud" disabled className="min-h-[44px] flex-1 touch-manipulation" />
              <Button type="button" variant="outline" size="icon" className="min-h-[44px] min-w-[44px] shrink-0 touch-manipulation sm:min-h-0 sm:min-w-0" disabled aria-label="Speak"> <Volume2Icon /> </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (!sttSupported) {
    return (
      <Card className={cn("rounded-xl border-border/80 shadow-sm", className)} role="region" aria-labelledby="voice-section-title">
        <CardHeader className="pb-2">
          <CardTitle id="voice-section-title" className="text-base font-medium text-foreground">Voice input</CardTitle>
          <CardDescription>
            Speech recognition is not supported in this browser. Try Chrome or Edge.
          </CardDescription>
        </CardHeader>
      </Card>
    );
  }

  return (
    <Card className={cn("rounded-xl border-border/80 shadow-sm", className)} role="region" aria-labelledby="voice-section-title">
      <CardHeader className="pb-2">
        <CardTitle id="voice-section-title" className="text-base font-medium text-foreground">Voice input</CardTitle>
        <CardDescription className="text-sm">
          Speak your query or hear findings read aloud.
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* STT: Mic + transcript */}
        <div className="space-y-2">
          <div className="flex flex-wrap items-center gap-2 sm:flex-nowrap">
            <Button
              type="button"
              variant={isListening ? "destructive" : "default"}
              size="icon"
              className="min-h-[44px] min-w-[44px] touch-manipulation sm:min-h-0 sm:min-w-0"
              onClick={handleToggleMic}
              aria-label={isListening ? "Stop listening" : "Start listening"}
              aria-pressed={isListening}
            >
              {isListening ? <MicOffIcon /> : <MicIcon />}
            </Button>
            <span className="text-sm text-muted-foreground">
              {isListening ? "Listening… tap mic to stop and send" : "Tap mic to speak your question"}
            </span>
          </div>
          <div
            className="min-h-10 rounded-lg border border-input bg-background/50 px-3 py-2 text-sm text-muted-foreground"
            role="status"
            aria-live="polite"
            aria-atomic="false"
            aria-label="Live transcript"
          >
            {displayTranscript}
          </div>
          {sttError && (
            <p className="text-sm text-destructive" role="alert">
              {sttError}
            </p>
          )}
        </div>

        {/* TTS: input + speak */}
        <div className="space-y-2">
          <label htmlFor="voice-speak-input" className="text-sm font-medium">
            Hear findings aloud
          </label>
          <div className="flex flex-wrap gap-2 sm:flex-nowrap">
            <Input
              id="voice-speak-input"
              type="text"
              placeholder={textToSpeak ? "Or type here…" : "Enter text to hear it read aloud"}
              value={speakInput}
              onChange={(e) => setSpeakInput(e.target.value)}
              onKeyDown={(e) => e.key === "Enter" && handleSpeakInput()}
              className="min-h-[44px] flex-1 touch-manipulation"
            />
            <Button
              type="button"
              variant="outline"
              size="icon"
              className="min-h-[44px] min-w-[44px] shrink-0 touch-manipulation sm:min-h-0 sm:min-w-0"
              onClick={handleSpeakInput}
              aria-label="Speak the text"
              disabled={!speakInput.trim() && !textToSpeak}
            >
              <Volume2Icon />
            </Button>
          </div>
          {textToSpeak && (
            <Button
              type="button"
              variant="secondary"
              size="sm"
              onClick={() => speakText(textToSpeak)}
              className="min-h-[44px] w-full touch-manipulation"
            >
              Speak last findings
            </Button>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
